header {
    width: 100%;
    display: flex;
    justify-content: center;
    box-shadow: 0 1px 5px 0 rgba(175, 175, 175, 0.1);
}

.navbar {
    padding: 15px;
    height: 12vh;
    width: 100%;
    max-width: 1300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-logo {
    height: 90%;
}

.nav-logo-img {
    height: 100%;
    width: auto;
}

.nav-headers {
    display: flex;
    gap: 30px;

    a {
        color: black;
        text-decoration: none;
    }

    a.active {
        color: rgba(0, 0, 0, 0.7);
    }
}

